function convertUnits(value, fromUnit, toUnit) {
    const additiveFactor1 = exports.reverseUnitsLookupTable[fromUnit].additiveConversionFactor;
    const multiplicativeFactor1 = exports.reverseUnitsLookupTable[fromUnit].multiplicativeConversionFactor;
    const baseUnitValue = (value - additiveFactor1) / multiplicativeFactor1;
    const additiveFactor2 = exports.reverseUnitsLookupTable[toUnit].additiveConversionFactor;
    const multiplicativeFactor2 = exports.reverseUnitsLookupTable[toUnit].multiplicativeConversionFactor;
    const toUnitValue = (baseUnitValue * multiplicativeFactor2) + additiveFactor2;
    return toUnitValue;
}
